/*
To customize the look and feel of Ionic, you can override the variables
in ionic's _variables.scss file.

For example, you might change some of the default colors:

$light:                           #fff !default;
$stable:                          #f8f8f8 !default;
$positive:                        #387ef5 !default;
$calm:                            #11c1f3 !default;
$balanced:                        #33cd5f !default;
$energized:                       #ffc900 !default;
$assertive:                       #ef473a !default;
$royal:                           #886aea !default;
$dark:                            #444 !default;
*/

//  jspm_packages  ionic 
// The path for our ionicons font files, relative to the built CSS in www/bundles
$ionicons-font-path: "../jspm_packages/github/driftyco/ionic-bower@1.0.0/fonts" !default;
@import "www/jspm_packages/github/driftyco/ionic-bower@1.0.0/scss/ionic.scss";  //  ionic  scss 

@import "./helper.scss"; // 

//  header bar 
.button-bar-icon {
  .icon:before,
  &.icon:before {
    font-size: $button-bar-button-icon-size !important;
  }
}

// 
.item-text-wrap .item,
.item-text-wrap .item-content,
.item-text-wrap,
.item-text-wrap h1,
.item-text-wrap h2,
.item-text-wrap h3,
.item-text-wrap h4,
.item-text-wrap h5,
.item-text-wrap h6,
.item-text-wrap p {
  word-break: break-all;  // 
  hyphens: auto;          // 
}

// 
.topic-item {
  .topic-item-meta {
    //  small  margin 
    margin-bottom: -4px !important;
  }

  small:not(:last-of-type), .icon {
    margin-right: 0.25em;
  }

  .topic-item-boardname {
    background-color: #f5f5f5;
    color: #999;
    padding: 2px 4px;
    border-radius: 2px;
  }
}

//  16px  h3  14px
.scroll-refresher {
  .ionic-refresher-content {
    bottom: 16px !important;
    .text-refreshing,
    .text-pulling {
      font-size: 14px !important;
      line-height: 14px !important;
    }
  }
}

// 
.topic-detail {
  .posts-from {
    @extend .item;
    @extend .balanced;

    padding-top: 6px;
    padding-bottom: 6px;
    min-height: 24px;
    border: none;

    font-size: 12px;
    font-weight: 100;
    background-color: #f8f8f8;
  }

  .post-item:nth-child(2) {
    border-top: none;
  }
}
